<template>
  <div>

    <van-search
      v-model="keyword"
      placeholder="永生花"
      show-action
      @search="onSearch"
      @cancel="onCancel"
    />
    <div class="search_two_hot">
        <h4>热门搜索</h4>
        <p>
            <a href="">2月14情人节鲜花</a>
            <a href="">爱情鲜花</a>
        </p>
    </div>

    <div>
      <List 
        :goods="goods"
          @changeallgoods="getGoods"
        >
        </List>
    </div>

  </div>

</template>

<script>
export default {
  data() {
    return {
      keyword: "",
      goods:[]
    };
  },
  methods: {
     getGoods(){
            this.goods.reverse();
      },
    onSearch() {
      // enter 键盘的回车
     
   
     this.$axios.get("/vue/getGoods",{
        params:{
          keyword:this.keyword
        }
     }).then(res=>{
        this.goods = res.data.result;
      })
    },
    onCancel() {
        this.$router.go(-1);
    }
  }
};
</script>

<style  scoped>
    .search_two_hot{
      
        margin-top: 0.5rem;
        margin-left: 0.6rem;
        color: #666;
        line-height: 0.5rem;
        overflow:hidden;
    }
    .search_two_hot>h4{
        margin-bottom:0.2rem;
    }
    .search_two_hot>p>a{
        display:inline-block;
        border: 1px solid #999;
        font-size: 0.2rem;
        color:#666;
        margin: 0 0.15rem ;
        padding: 0 0.2rem;
    }

</style>